import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './history.scss';
import ipfs from 'decentralized/Ipfs.js';

const History = ({latestJacks, loadHistory}) => { 
    console.log(latestJacks);
    const load = useCallback((x) => {
        ipfs.getJson(x.data).then(
            y => loadHistory(y));
    }, [loadHistory]);

    return (<div className="jack-history">
              <img className="globe" src="/globe.svg"/>
              <div className="items">
              {latestJacks.map(
                  x => <div
                         onClick={() => load(x)}
                         className="history-item">
                         {x.title}
                       </div>)}

              </div>
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(History);
