
import ipfs from 'decentralized/Ipfs.js';
export const save = async (json) => {
    let hash = await ipfs.addJson(json);
    // save the hash and name

    fetch("https://zequencer.io/saveJack", {
        method: "POST",
         headers: {
             'Accept': 'application/json',
             'Content-Type': 'application/json'
         },
        body: JSON.stringify({
            data: hash,
            title: json.name,
        })
    });
};
